<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process161"></a>

<p><br></p>
<p><span class="rvts17">账本上的红墨迹（</span><span class="rvts50">The Red Ink in the Ledger</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:319,Y:148</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：加文（</span><span class="rvts20">Garvan</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">报告加文关于他的物资队情况</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1130.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在浮木城酒馆里，可以找到加文在饭桌上独自品菜。与他对话，他会假装一脸的苦闷，并让调查商队被袭击的地方。</span>
<br><span class="rvts21">加文是一个狡诈却又城府的商人，他喜欢吃矮人煮饭，并抱怨酒吧的饭菜味道极差。可以首先记住他这一个弱点。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1131.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">之后来到浮木城西边靠近废墟兵营的地方（离有一只大熊守着的桥很近，位置</span><span class="rvts20">X:202,Y:173</span><span class="rvts21">），尝试打开这里的箱子，接着便会遭遇伏击。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1132.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在遭遇伏击后，可以在旁边一大滩血处发现一个砂土坑，挖开砂土坑是一堆尸骨（位置</span><span class="rvts20">X:201,Y:165</span><span class="rvts21">），这便是商人莱姆（</span><span class="rvts20">Liam</span><span class="rvts21">）遇害的地方。</span>
<br><span class="rvts21">尝试使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">与莱姆对话，莱姆会指出陷害他的并不是那些被虚空占据灵魂的矮人，而是狡诈的加文。到这里，任务将触发</span><span class="rvts20"></span><span class="rvts21">暴力接管</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts21">当完成了</span><span class="rvts20"></span><span class="rvts21">暴力接管</span><span class="rvts20"></span><span class="rvts21">支线后，该支线</span><span class="rvts20"></span><span class="rvts21">账本上的红墨迹</span><span class="rvts20"></span><span class="rvts21">也将同时关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



